router.beforeEach((to, from, next) => {
  const publicPages = ["/login", "/forgot", "/reset", "/activate"];
  const authRequired = !publicPages.includes(to.path);
  const loggedIn = localStorage.getItem("user");

  // Check if route requires authentication
  if (authRequired && !loggedIn) {
    next("/login");
    return;
  }

  // Check for role-based permissions (including nested/child routes)
  // Find the first route record with meta.roles in the matched route chain
  const matchedWithRoles = to.matched.find(
    (record) => record.meta && record.meta.roles
  );
  if (matchedWithRoles && loggedIn) {
    const user = JSON.parse(localStorage.getItem("user"));
    const hasRequiredRole =
      user &&
      user.roles &&
      matchedWithRoles.meta.roles.some((role) => user.roles.includes(role));

    if (!hasRequiredRole) {
      // Redirect to 404 using path, not named route with param
      next("/notfound");
      return;
    }
  }

  next();
});
